<template>
  <div class="cplx">
    <!---->
    <div class="toolbar-top">
      <!-- <span></span> -->
      <el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getFilterData" icon="el-icon-search" circle></el-button>
				</el-form-item>
        <el-form-item>
					<el-button type="primary" @click="handleAdd" icon="el-icon-plus" circle></el-button>
				</el-form-item>
			</el-form>
   
      <!-- <el-button class="tool-btn" type="primary" icon="el-icon-edit" @click="handleEditBtn" circle></el-button> -->
    </div>
    <!-- :highlight-current-row="editing" -->
    <el-table v-loading="listLoading" :data="tablePage"  
              border :max-height="height" @row-dblclick="handleDbclickRow" 
              @row-click ="handleClickRow" 
              :header-row-style="rowStyle"
              @selection-change="selsChange"
              >
      <el-table-column type="selection" width="50">
			</el-table-column>
      <el-table-column prop="jsdm" label="" sortable>
        <template slot-scope="scope">
          <el-input v-if="scope.row.editing"  size="small" v-model="scope.row.jsdm" 
                    placeholder="" 
                    @change="handleEdit(scope.$index, scope.row)"
                    @blur="handleBlur">
          </el-input>
          <span v-if="!scope.row.editing">{{scope.row.jsdm}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="jsmc" label="" sortable>
        <template slot-scope="scope">
          <el-input v-if="scope.row.editing" size="small" v-model="scope.row.jsmc" placeholder="" 
                    @change="handleChange(scope.$index, scope.row)" 
                    @blur="handleBlur">
          </el-input>
          <span v-if="!scope.row.editing">{{scope.row.jsmc}}</span>
        </template>
      </el-table-column>

      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            v-show="!scope.row.editing"
            size="mini"
            @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button
            v-show="scope.row.editing"
            size="mini"
            type="primary"                        
            @click="handleSave(scope.$index, scope.row)"></el-button>
          <el-button
            v-show="scope.row.editing"
            size="mini"
            @click="handleCancel(scope.$index, scope.row)"></el-button>
          <el-button
            v-show="!scope.row.editing"            
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row, tablePage)"></el-button>
        </template>
      </el-table-column>
    </el-table>
   

    <!---->
		<el-col :span="24" class="toolbar-bottom">
      <!-- @click="batchRemove" :disabled="this.sels.length===0" -->
			<el-button plain @click="batchDelete" type="danger" :disabled="this.sels.length===0" icon="el-icon-delete"></el-button>
      <el-pagination
        background
        layout="prev, pager, next"
        @current-change="handleCurrentChange"
        :page-size= "pageSize"
        :current-page="currentPage"
        :total="total">
      </el-pagination>
		</el-col>

  
  </div>
</template>

<script>
import axios from 'axios'
const ADDRESS_JSXX ='api/systable/jsxx'

export default {
  name: 'Jsxx',
  data() {
    return {
      pageSize: Math.floor((window.innerHeight - 230)/53),
      // pageSize: 20,
      currentPage:1,
      tableData: [],
      total: 0,
      listLoading: false,
      sels: [],//
      filters: {
					name: ''
        },
      filterData:[]
    }
  },
  computed:{
    tablePage () {
      return this.tableData.slice((this.currentPage - 1) * this.pageSize, this.currentPage * this.pageSize)
    },
    height () {
      return (this.pageSize + 1) *59
    }

  },
  methods:{
    getFilterDataSucc(res) {
      this.listLoading = false;      
      res = res.data;
      if (res.code === '0000' && res.data) {
        this.tableData = res.data;
        this.total = res.data.length;
      }
      this.filterData = [];

      console.log(this.tableData);
      

      if (this.filters.name !== '') {    
          this.tableData.map((item) => {
            if (item.jsmc.indexOf(this.filters.name) !== -1) {
              this.filterData.push(item);         
          }
        });

        console.log(this.filterData);
        this.tableData = this.filterData;
        console.log(this.filterData.length);
        // this.tableData = this.filterData.slice(0,this.filterData.length);
        this.total = this.tableData.length;
        console.log(this.tableData);
        
      } 

    },


    getFilterData (){
      this.listLoading = true;      
      // 
      this.currentPage = 1;
      // axios.get('/api/jsxx.json').then(this.getFilterDataSucc);
      axios.get(ADDRESS_JSXX).then(this.getFilterDataSucc);
      
    },

    handleCurrentChange(curr) {
      console.log(this.tablePage);
      for (let i = 0; i < this.tablePage.length; i++) {
        let item = this.tablePage[i];
        if (item.editing) {
          item.editing = false;
        } 
      }

      this.currentPage = curr;
      console.log(this.tableData);
      
    },

    getJsxx () {
      this.listLoading = true;
      // axios.get('/api/jsxx.json')
      // .then(this.getJsxxSucc)
      axios.get(ADDRESS_JSXX)
      .then(this.getJsxxSucc)
    },
    getJsxxSucc (res) {
      this.listLoading = false;
      console.log(res);
      res = res.data;
      if (res.code === '0000' && res.data) {
        this.tableData = res.data;
        this.total = res.data.length;
      }
    },
    handleChange(index, row) {
      console.log(index);
      console.log(row);
    },
    handleDbclickRow (row) {
      // this.tableData.map(function(item){  
      //   item.editing = false
      // });
      // row.editing = true;
      // console.log(row.editing);
      
    },
    handleClickRow () {
      
    },
    handleBlur () {
        // this.tableData.map(function(item){  
        // item.editing = false
      // });
    },
    handleEditBtn (){
      this.editing = !this.editing
    },
    handleAdd (){
      console.log(this.tablePage);
      if(this.tablePage[this.tablePage.length - 1].jsdm === ''){
        this.$message({
              message: '',
              type: 'warning'
            });
      } else {
        this.currentPage = Math.floor(this.total/this.pageSize) + 1;
        this.tablePage.map((item) => {  
          item.editing = false
        });
        this.tablePage.push({"jsdm":"","jsmc":"","editing":true});
      }
      
    },
    handleEdit(index, row) {
      row.editing = !row.editing;
      this.tablePage.map((item) => {
        item.editing = false
      });
      row.editing = true;
      console.log(index);
      console.log(row);
    },

    handleCancel(index,row){
      row.editing = !row.editing;
      this.$message({
          type: 'info',
          message: ''
        });
      
      //
      if (this.tablePage.length === 1) {
        this.currentPage = this.currentPage - 1;
      }
      this.getJsxx(); 
       
    },

     handleSave(index, row) {
       row.editing = !row.editing;

       if (!row.index) {
         this.$confirm('?', '', {
           confirmButtonText: '',
           cancelButtonText: '',
           type: 'warning'
         }).then(() => {
           //
           axios.post(ADDRESS_JSXX, {
             'jsdm': row.jsdm,
             'jsmc': row.jsmc,
             'xw': '1'
           }).then((res) => {
             if (res.data.code === '0000') {
               this.$message({
                 showClose: true,
                 message: '',
                 type: 'success'
               });
             } else {
               this.$message({
                 showClose: true,
                 message: res.data.msg,
                 type: 'info'
               });
             }
             this.getJsxx();
             this.currentPage = 1;
           }).catch(() => {
             this.$message({
               showClose: true,
               message: '',
               type: 'info'
             });
           })
         }).catch(() => {
           this.getJsxx();
           this.$message({
             showClose: true,
             message: '',
             type: 'info'
           });
         });


       } else {
         this.$confirm('?', '', {
           confirmButtonText: '',
           cancelButtonText: '',
           type: 'warning'
         }).then(() => {
           //

           axios.post(ADDRESS_JSXX, {
             'index': row.index,
             'jsdm': row.jsdm,
             'jsmc': row.jsmc,
             'xw': '0'
           }).then((res) => {
             if (res.data.code === '0000') {
               this.$message({
                 showClose: true,
                 message: '',
                 type: 'success'
               });
             } else {
               this.$message({
                 showClose: true,
                 message: res.data.msg,
                 type: 'info'
               });
             }
           }).catch(() => {

           })
         }).catch(() => {
           this.getJsxx();
           this.$message({
             showClose: true,
             message: '',
             type: 'info'
           });
         });
       }
     },


    handleDelete(index, row, rows) {
      console.log(rows);
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        //
        let deleteIndex = [];
        deleteIndex.push(row.index);
        axios.post(ADDRESS_JSXX, {
          'index': deleteIndex,
          'xw': '2'
        }).then((res) => {
          if (res.data.code === '0000') {
            rows.splice(index, 1);
            this.$message({
              type: 'success',
              message: '!'
            });
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg,
              type: 'info'
            });
          }
          //
          if (this.tablePage.length === 0) {
            this.currentPage = this.currentPage - 1;
          }
          this.getJsxx();

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });

      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },

    selsChange (sels){
      this.sels = sels;
      console.log(sels);
      
    },

    batchDelete() {
      console.log(this.sels);

      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let deleteIndex = [];
        this.sels.map((sel) => {
          deleteIndex.push(sel.index);
        });
        console.log(deleteIndex);
        axios.post(ADDRESS_JSXX, {
          'index': deleteIndex,
          'xw': '2'
        }).then((res) => {
          if (res.data.code === '0000') {
            this.$message({
              type: 'success',
              message: '!'
            });
          } else {
            this.$message({
              showClose: true,
              message: res.data.msg,
              type: 'info'
            });
          }
          
          this.getJsxx();
          
          this.currentPage = 1;

        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      }).catch(() => {
        // this.$message({
        //   type: 'info',
        //   message: ''
        // });
      });
    },

    rowStyle(row) {
      return 'color:green'
    }
  },
  mounted () {
      this.getJsxx() 
    
  }
}
</script>

<style lang="stylus" scoped>
.cplx
  padding .2rem
  .toolbar-top
    .el-form
      height .8rem
    span
      font-size .44rem
      margin-left .2rem
  .el-table
    margin-top .2rem
    width 100%
    // .el-input
    //   display none
    // .current-row
    //   span
    //     display none
    //   .el-input
    //     display inline
  .toolbar-bottom
    background #eee
    padding .1rem
    .el-pagination
      float right 
// .editing
//   span
//     display none
//   .el-input
//     display inline
</style>




